.contacts {
  padding: 90px 15px 90px;
  text-align: center;
  position: relative;
  overflow: hidden;
  max-height: 500px;

  @media screen and (min-width: $medium-screen) {
    padding: 140px 15px 230px;
    margin-bottom: 270px;
  }
}

.contacts__item {
  color: $grey;
  display: block;
  font-size: 1rem;
  line-height: 2;
  margin: 10px auto 0;

  &:first-child {
    margin-top: 35px;

    @media screen and (min-width: $medium-screen){
      margin-top: 135px;
    }
  }

  &:hover {
    text-decoration: underline;
  }

  &:last-child {
    font-weight: bold;

    &:hover {
      text-decoration: none;
    }
  }
}

.contacts__img {
  position: absolute;
  top: 270px;
  left: 50%;
  transform: translateX(-50%);
  z-index: -1;
  display: none;

  @media screen and (min-width: $medium-screen){
    display: block;
  }
}

.plane {
  position: absolute;
  animation: move 12s linear infinite;
  bottom: -190px;
  left: 6.5%;
  transform: rotate(0) translate(0, 0);
  transform-origin: center center;
  display: none;

  @media screen and (min-width: $medium-screen){
    display: block;
  }
}

@keyframes move {
  0% {
    transform-origin: center center;

    transform: rotate(-90deg) translate(0, 0);
  }

  50% {
    transform-origin: center center;

    transform: rotate(-25deg) translate(800px, 0);
  }


  100% {
    transform-origin: center center;

    transform: rotate(20deg) translate(1350px, 0);
  }
}