.footer-wrapper {
  background-color: $light-green;
  padding: 35px 15px;

  @media screen and (min-width: $medium-screen) {
    padding: 80px 15px;
  }
}

.footer {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  flex-direction: column;
  text-align: center;

  @media screen and (min-width: $medium-screen) {
    flex-wrap: nowrap;
    flex-direction: row;
    text-align: left;
    justify-content: space-between;
  }

  .policy__link {
    font-size: 1rem;
    margin-top: 15px;
  }

  .navigation {
    display: none;

    @media screen and (min-width: $medium-screen){
      display: flex;
    }
  }
}


.footer-options {
  justify-content: space-between;
  margin-top: 20px;
  display: none;

  @media screen and (min-width: $medium-screen) {
    margin-top: 0;
    display: flex;
  }
}