.burger {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: none;
  padding: 120px 16% 0;
  flex-direction: column;
  justify-content: center;
  background-color: #fff;

  &.is-open {
    display: flex;
  }

  .navigation {
    flex-direction: column;
    margin-right: 0;
  }

  .navigation__link {
    font-size: 36px;
    margin-bottom: 20%;
  }

  .header-options {
    flex-direction: column;
  }

  .language {
    margin-left: 0;
    margin-top: 20px;
  }

  .burger-close {
    position: absolute;
    top: 40px;
    right: 4%;
    outline: 0;
    cursor: pointer;
    transition: transform 0.7s;

    &:hover{
      transform: rotate(360deg);
      transform-origin: center;
    }
  }

  .order-link {
    margin: 0;
  }
}

.burger-btn {
  position: absolute;
  top: 40px;
  right: 4%;
  outline: 0;
  cursor: pointer;

  @media screen and (min-width: $medium-screen) {
    display: none;
  }
}