.portfolio {
  padding: 110px 15px;
}

.case-wrapper {
  margin-top: 50px;
  display: flex;
  flex-wrap: wrap;

  @media screen and (min-width: $medium-screen) {
    margin-top: 80px;
  }
}

.case {
  margin: 20px auto;
  flex: 1 1 auto;
  transition: all 1s ease;
  text-align: center;
  max-width: 700px;
  height: 360px;
  position: relative;
  z-index: 0;
  padding: 39px 0 41px;
  width: 50%;
  box-shadow: 0 20px 50px rgba(0, 0, 0, 0.1);

  @media screen and (min-width: $tablet-screen) {
    width: 30%;
  }

  strong {
    font-weight: bold;
  }

  &:hover {
    cursor: pointer;
    flex: 5 1 auto;

    .button,
    .case__description,
    .case__logo {
      opacity: 1;
      z-index: 3;
    }
    &::after {
      content: '';
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      z-index: -1;
      background-color: rgba(255, 255, 255, 0.83);
    }
  }

  .button {
    width: 150px;
    margin: 47px auto 0;
    object-position: 0;
    opacity: 0;

    &:hover {
      background-color: $forest-green;
      color: #fff;
      box-shadow: 0 20px 40px rgba(0, 145, 131, 0.2);
    }
  }
}

.case__description {
  margin-top: 52px;
  line-height: 24px;
  opacity: 0;
  font-size: 10px;
}

.case__logo {
  opacity: 0;
}

.case__img {
  position: absolute;
  left: 0;
  top: 0;
  object-fit: cover;
  z-index: -1;
  height: 100%;
  opacity: 1;
}