.section-heading {
  font-size: 2.25rem;
  letter-spacing: 0.45rem;
  font-weight: 900;
  text-align: center;
  line-height: 1.2;
  text-transform: capitalize;
}

.section-caption {
  font-size: 1rem;
  line-height: 2;
  text-align: center;
  color: $grey;
  letter-spacing: 0.24px;
  margin-top: 25px;

  @media screen and (min-width: $tablet-screen) {
    margin-top: 50px;
  }
}