.single-case {
  margin-top: 30px;
  position: relative;
  padding: 0 15px 60px;

  @media screen and (min-width: $medium-screen) {
    margin-top: 180px;
  }
}

.case-info {
  padding-top: 450px;

  @media screen and (min-width: $medium-screen) {
    width: 45%;
    padding-top: 0;
  }
}

.case-img {
  position: absolute;
  right: 0;
  top: 0;
  max-width: 73%;
  height: 450px;
  object-fit: cover;

  @media screen and (min-width: $medium-screen) {
    height: 93%;
    transform: translateX(43%);
  }
}

.case-info__heading {
  font-weight: bold;
  line-height: 16px;
  margin-top: 41px;
  text-transform: uppercase;
  color: $forest-green;
}

.case-info__text {
  line-height: 33px;
  letter-spacing: 0.3px;
  margin-top: 14px;
}

.case-download {
  margin-right: 30px;
}

.case-download,
.case-order {
  display: inline-block;
  margin-top: 50px;
  width: 170px;
  font-weight: 900;
}

.case-order {
  margin-top: 30px;

  @media screen and (min-width: $medium-screen) {
    margin-top: 50px;
  }
}