*,
*::after,
*::before {
  box-sizing: border-box;
}

html, body {
  overflow-x: hidden;
}

html {
  font-size: 14px;

  @media screen and (min-width: $tablet-screen){
    font-size: 16px;
  }
}

body {
  font-family:  'Gotham Pro';
  font-weight: normal;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}