.error {
  padding: 30px 15px 100px;
  position: relative;

  &:before {
    content: '404';
    position: absolute;
    top: 23%;
    right: 2%;
    font-weight: 600;
    font-size: 100px;
    color: #009183;

    @media screen and (min-width: $mobile-screen) {
      top: 13%;
      font-size: 250px;
    }

    @media screen and (min-width: $medium-screen) {
      font-size: 340px;
      top: 50%;
      transform: translateY(-50%);
      right: 13%;
    }
  }

  .error-text {
    text-transform: capitalize;
    color: #000;
    font-size: 24px;
    font-weight: 300;
    top: 290px;
  }

  .button--back {
    bottom: 213px;
  }

  .intro__img {
    &::before {
      background: $light-green;
    }
  }
}