.header-wrapper {
  width: 100%;
  padding: 25px 15px;
  z-index: 3;
  position: fixed;

  .container {
    flex-wrap: wrap;

    @media screen and (min-width: $medium-screen) {
      flex-wrap: nowrap;
      display: flex;
      justify-content: space-between;
    }
  }
}

.header {
  display: none;
  width: 70%;

  .navigation {
    @media screen and (min-width: $mobile-screen) {
      margin-right: 15%;
    }
  }

  @media screen and (min-width: $medium-screen) {
    display: flex;
    justify-content: space-between;
  }

  .policy__link {
    margin-top: 23px;
    font-size: 12px;
  }
}

.header-options {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;

  @media screen and (min-width: $medium-screen) {
    margin-top: 0;
  }
}

.language {
  margin-left: 50px;
  z-index: 3;
  position: relative;

  img {
    margin-left: 8px;
  }
}

.is-hidden {
  position: absolute;
  z-index: 3;
  top: 60px;
  display: none;
  left: 35px;
}

.is-rotated {
  transform: rotate(180deg);
  transition: all 0.2s ease;
}

.language__option {
  color: $forest-green;
  z-index: 3;
  cursor: pointer;
}