.thanks {
  background: url("../images/thanks-bg.svg") no-repeat right 18vh;
  padding: 50vh 0 30vh;
}

.thanks__heading {
  font-size: 64px;
  font-weight: bold;
  text-transform: capitalize;
}

.thanks__link {
  margin-top: 55px;
  width: 170px;

  &:hover {
    background-color: $forest-green;
    color: #fff;
  }
}