.intro {
}

.intro__slide {
  height: 80vh;
  position: relative;

  @media screen and (min-width: $mobile-screen) {
    height: 100vh;
  }
}

.slide__img {
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  position: absolute;
  width: 100%;
  z-index: -1;
  object-fit: cover;

  img {
    object-fit: cover;
  }
}

.slide__text {
  line-height: 1.2;
  font-weight: 900;
  display: block;
  font-size: 2.25rem;
  letter-spacing: 0.45rem;
  color: #000;
  margin-top: 50%;
  margin-left: 5%;
  max-width: 520px;

  @media screen and (min-width: $medium-screen) {
    margin-top: 15%;
    margin-left: 32%;
  }
}
