.order-modal {
  position: fixed;
  top: 20%;
  left: 50%;
  transform: translateX(-50%);
  background: #fff;
  max-width: 570px;
  width: 90vw;
  height: 420px;
  padding: 60px 70px;
  display: none;
  z-index: 10;
  box-shadow: 5px 5px 40px rgba(0, 0, 0, 0.2);


  &.is-shown {
    display: block;
  }
}

.order-modal__field {
  border: 1px solid $green;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  padding: 10px;
}

.order-modal__field-label {
  color: $grey;
  font-size: 14px;
  margin: 20px 0 6px;
  display: block;

  &:first-child {
    margin-top: 0;
  }
}

.order-modal__submit {
  margin: 40px auto;

  &:hover {
    background-color: $forest-green;
    color: #fff;
  }
}