.button {
  width: 130px;
  border: $forest-green 2px solid;
  text-align: center;
  padding: 11px 0;
  color: $forest-green;
  border-radius: 25px;
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.2s ease;
  display: block;
  letter-spacing: 0.3px;
  outline: 0;
  text-transform: capitalize;

  &:hover {
    box-shadow: 0 10px 40px rgba(0, 145, 131, 0.2);
  }
}

.button--green {
  background-color: $forest-green;
  color: #fff;

  &:hover {
    box-shadow: 0 10px 40px rgba(0, 145, 131, 0.2);
  }
}